  border: 1px solid rgba(91, 135, 244, 0.4) !important;
  border-radius: 999px !important;
  background: linear-gradient(135deg, rgba(33, 104, 255, 0.94), rgba(14, 195, 249, 0.9)) !important;
  color: #ffffff !important;
  font-weight: 700 !important;
  font-size: 0.82rem !important;
  letter-spacing: 0.03em;
  padding: 0.58rem 0.88rem !important;
  box-shadow: 0 12px 22px rgba(13, 78, 211, 0.3);
  transition: transform 180ms ease, box-shadow 180ms ease;
  transition: transform var(--transition-fast), box-shadow var(--transition-fast);
}

.theme-toggle-btn:hover,
.theme-toggle-btn:focus {
  transform: translateY(-1px) scale(1.01);
  box-shadow: 0 16px 30px rgba(9, 74, 206, 0.35);
}

.theme-toggle-orb {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: radial-gradient(circle at 30% 30%, #ffe498, #ffb66e);
  box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.28);
  transition: transform 180ms ease, background 180ms ease;
  transition: transform var(--transition-fast), background var(--transition-fast);
}

.theme-toggle-orb.is-dark {
  background: radial-gradient(circle at 35% 32%, #b8ddff, #7f9bff);
  transform: rotate(-28deg);
}

.theme-toggle-text {
  line-height: 1;
  white-space: nowrap;
}

.page-shell {
  display: grid;
  grid-gap: 1rem;
  gap: 1rem;
  margin-top: 0.6rem;
}

.status-alert {
  border: 1px solid var(--surface-border) !important;
  border-radius: 14px !important;
  padding: 0.8rem 1rem !important;
  background: var(--surface-muted) !important;
  color: var(--text-primary) !important;
  box-shadow: var(--shadow-soft);
}

@-webkit-keyframes appContentEnter {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes appContentEnter {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@-webkit-keyframes driftOne {
  0%,
  100% {
    transform: translate(0, 0);
  }

  50% {
    transform: translate(16px, 12px);
  }
}

@keyframes driftOne {
  0%,
  100% {
    transform: translate(0, 0);
  }

  50% {
    transform: translate(16px, 12px);
  }
}

@-webkit-keyframes driftTwo {
  0%,
  100% {
    transform: translate(0, 0);
  }

  50% {
    transform: translate(-18px, -14px);
  }
}

@keyframes driftTwo {
  0%,
  100% {
    transform: translate(0, 0);
  }

  50% {
    transform: translate(-18px, -14px);
  }
}

@media (max-width: 991px) {
  .gb-navbar {
    padding: 0.85rem 0.9rem;
  }

  .gb-nav-links {
    margin-top: 0.75rem;
    align-items: stretch;
  }

  .gb-nav-link {
    padding: 0.56rem 0.8rem !important;
  }

  .theme-toggle-btn {
    margin-top: 0.5rem;
    margin-left: 0;
    justify-content: center;
  }

  .gb-brand-mark-wrap {
    width: 50px;
    height: 50px;
  }

  .gb-brand-mark {
    width: 34px;
    height: 34px;
  }
}

@media (max-width: 575px) {
  .app-content,
  .gb-navbar {
    width: calc(100% - 1rem);
  }

  .app-content {
    padding-top: 0.9rem;
  }

  .gb-brand-title {
    font-size: 1.15rem;
  }

  .gb-brand-subtitle {
    font-size: 0.66rem;
  }
}

@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    -webkit-animation-duration: 0.01ms !important;
            animation-duration: 0.01ms !important;
    -webkit-animation-iteration-count: 1 !important;
            animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

